.block--c .faq-questions-wrap {
  padding-top: 25px;
}
@media screen and (min-width: 1025px) {
  .block--c .faq-questions-wrap {
    padding-top: 1.458vw;
  }
}
@media screen and (min-width: 1921px) {
  .block--c .faq-questions-wrap {
    padding-top: 28px;
  }
}
.block--c .faq-question {
  position: relative;
  border-bottom: 1px solid #002ea6;
}
.block--c .faq-question.preopened-item .dropdown-content-wrap {
  height: auto;
}
.block--c .faq-question.preopened-item .dropdown-arrow-btn {
  transform: rotate(-180deg) translateY(50%);
}
.block--c .faq-question.opened .dropdown-arrow-btn {
  transform: rotate(-180deg) translateY(50%);
}
.block--c .faq-question-title {
  position: relative;
  cursor: pointer;
  width: 100%;
  padding: 20px 70px 20px 0px;
}
@media screen and (min-width: 1025px) {
  .block--c .faq-question-title {
    padding: 1.042vw 3.646vw 1.042vw 0vw;
  }
}
@media screen and (min-width: 1921px) {
  .block--c .faq-question-title {
    padding: 20px 70px 20px 0px;
  }
}
.block--c .dropdown-content-wrap {
  overflow: hidden;
  height: 0;
}
.block--c .dropdown-content {
  padding-bottom: 25px;
}
@media screen and (min-width: 1025px) {
  .block--c .dropdown-content {
    padding-bottom: 1.667vw;
  }
}
@media screen and (min-width: 1921px) {
  .block--c .dropdown-content {
    padding-bottom: 32px;
  }
}
.block--c .dropdown-arrow-btn {
  display: inline-block;
  position: absolute;
  backface-visibility: hidden;
  transition: transform 0.3s cubic-bezier(0.61, 1, 0.88, 1);
  width: 30px;
  height: 30px;
  right: 7px;
  top: 50%;
  transform: translateY(-50%);
}
@media screen and (min-width: 1025px) {
  .block--c .dropdown-arrow-btn {
    width: 1.563vw;
    height: 1.563vw;
    right: 0.365vw;
  }
}
@media screen and (min-width: 1921px) {
  .block--c .dropdown-arrow-btn {
    width: 30px;
    height: 30px;
    right: 7px;
  }
}
.block--c .dropdown-arrow-btn::before {
  content: "";
  display: inline-block;
  position: absolute;
  backface-visibility: hidden;
  left: 50%;
  top: 50%;
  transition: transform 0.3s cubic-bezier(0.61, 1, 0.88, 1), border-color 0.3s cubic-bezier(0.61, 1, 0.88, 1);
  transform: translate(-50%, -50%) rotate(-45deg);
  border-left-style: solid;
  border-bottom-style: solid;
  border-color: #002ea6;
  width: 6px;
  height: 6px;
  border-left-width: 2.5px;
  border-bottom-width: 2.5px;
}
@media screen and (min-width: 1025px) {
  .block--c .dropdown-arrow-btn::before {
    width: 0.313vw;
    height: 0.313vw;
    border-left-width: 0.13vw;
    border-bottom-width: 0.13vw;
  }
}
@media screen and (min-width: 1921px) {
  .block--c .dropdown-arrow-btn::before {
    width: 6px;
    height: 6px;
    border-left-width: 2.5px;
    border-bottom-width: 2.5px;
  }
}